import ScrollToggle from "../lib/scroll/scrollToggle";

export default class {

    constructor() {
        this.addBtnClickEvent();
        new ScrollToggle();
    }

    addBtnClickEvent(){
        const likeBtn = document.querySelectorAll('.btn');
        likeBtn.forEach(el=>{
            el.addEventListener('click',(e)=>{
                this.onClickBtnHandler(e.currentTarget);
            })
        })
    }

    onClickBtnHandler(e){
        const span = e.querySelector('span');
         // already clicked
        if(e.dataset.click=="true"){
            e.dataset.click = false;
            span.innerHTML = 'Like';
        }else{
            e.dataset.click = true;
            span.innerHTML = '';
        }
    }


}