import axios from 'axios';
import btn from "./btn";
export default class{

    content = document.querySelector('.list');

    constructor() {
        this.onFetchDataHandler();
    }

    onFetchDataHandler(){
        axios.get('https://api.nasa.gov/planetary/apod?api_key=sQxYjDyefz3KLSwcJNO6TszUgQhTZNOrtEwzGmVV&count=10')
        .then((res)=>{
            this.onShowFetchDataHandler(res.data);
        }).catch(()=>{
            throw new Error('Network Error');
        });
    }

    onShowFetchDataHandler(data){
        let list = '';

        data.forEach(el=>{
            const {date,explanation,hdurl,title} = el;
            list +=  `<li class="item">
                            <div class="img-wrap motion">
                                <img src="${hdurl}" alt="Space picture">
                            </div>
                            <div class="txt">
                                <em class="tit motion">${title}</em>
                                <span class="date motion">${date}</span>
                                <p class="cont motion">
                                    ${explanation}
                                </p>
                                <button class="btn motion" data-click="false">
                                    <span>Like</span>
                                </button>
                            </div>
                        </li>`
        });

        this.content.insertAdjacentHTML("beforeend", list);
        new btn();
    }

}